color: #00BCD4;
}

.login.form .btn {
margin: 20px auto;
}

.login.form p {
font-style: italic;
}

.login.form a {
color: #00BCD4;
cursor: pointer;
}

.login .form .input{
margin:10px;
}

.login .form button{
  margin:10px;
  width:100px
}

.login .form p span{
color: yellow;
cursor: pointer;

}

